import { database } from '../config/firebase';
import { ref, set, get, remove, push, update } from 'firebase/database';

/**
 * 
 */
const generateRandomPassword = () => {
  const chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  let password = '';
  for (let i = 0; i < 8; i++) {
    password += chars.charAt(Math.floor(Math.random() * chars.length));
  }
  return String(password); // 
};

/**
 * 
 */
export const createTrip = async (tripName, adminUsername) => {
  try {
    const tripsRef = ref(database, 'trips');
    const newTripRef = push(tripsRef);

    await set(newTripRef, {
      name: tripName,
      admin: adminUsername,
      createdAt: new Date().toISOString(),
      members: {},
      expenses: {},
    });

    console.log(` : ${newTripRef.key}`);
    return { success: true, tripId: newTripRef.key, message: `${tripName}` };
  } catch (error) {
    console.error(' :', error);
    return { success: false, message: '' };
  }
};

/**
 *  - 
 * 
 */
export const addMemberToTrip = async (tripId, memberName) => {
  try {
    const adminUsername = process.env.REACT_APP_ADMIN_USERNAME;
    const adminPassword = process.env.REACT_APP_ADMIN_PASSWORD;

    let password;
    if (memberName === adminUsername) {
      password = String(adminPassword);
    } else {
      password = generateRandomPassword();
    }

    const memberRef = ref(database, `trips/${tripId}/members/${memberName}`);

    await set(memberRef, {
      password: String(password),
      joinedAt: new Date().toISOString(),
    });

    return { 
      success: true, 
      password: String(password), 
      message: `${memberName}` 
    };
  } catch (error) {
    return { success: false, message: '' };
  }
};

/**
 * 
 */
export const getAllTrips = async () => {
  try {
    const tripsRef = ref(database, 'trips');
    const snapshot = await get(tripsRef);

    if (snapshot.exists()) {
      return { success: true, trips: snapshot.val() };
    }
    return { success: true, trips: {} };
  } catch (error) {
    console.error(' :', error);
    return { success: false, message: '' };
  }
};

