import React, { useState, useEffect, useContext, useCallback } from 'react';
import { AuthContext } from '../context/AuthContext';
import { getExpenses, addExpense, updateExpense, deleteExpense, calculateDebts } from '../services/expenseService';
import { updateTripName, updateMemberName, removeMemberFromTrip, getTrip, updateTripEmoji } from '../services/tripService';

export const TripDetail = ({ tripId, trip, onBack, onTripsUpdated }) => {
  const { currentUsername, isAdmin } = useContext(AuthContext);
  const [expenses, setExpenses] = useState({});
  const [showAddForm, setShowAddForm] = useState(false);
  const [showDebts, setShowDebts] = useState(true);
  const [editingId, setEditingId] = useState(null);
  const [formData, setFormData] = useState({
    description: '',
    amount: '',
    paidBy: currentUsername,
    splitWith: [],
  });
  const [message, setMessage] = useState('');
  const [editingTripName, setEditingTripName] = useState(false);
  const [newTripName, setNewTripName] = useState(trip.name);
  const [editingMember, setEditingMember] = useState(null);
  const [newMemberNameValue, setNewMemberNameValue] = useState('');
  const [localTrip, setLocalTrip] = useState(trip);
  const [editingEmoji, setEditingEmoji] = useState(false);
  const [newEmoji, setNewEmoji] = useState(trip.emoji || '');

  const loadExpenses = useCallback(async () => {
    const result = await getExpenses(tripId);
    if (result.success) {
      setExpenses(result.expenses || {});
    }
  }, [tripId]);

  const loadTrip = useCallback(async () => {
    const result = await getTrip(tripId);
    if (result.success) {
      setLocalTrip(result.trip);
    }
  }, [tripId]);

  useEffect(() => {
    setLocalTrip(trip);
    setNewTripName(trip.name);
  }, [trip]);

  useEffect(() => {
    loadExpenses();
    loadTrip();
  }, [tripId, loadExpenses, loadTrip]);

  const handleAddExpense = async (e) => {
    e.preventDefault();
    setMessage('');

    if (!formData.description || !formData.amount) {
      setMessage(' ');
      return;
    }

    if (editingId) {
      const result = await updateExpense(tripId, editingId, {
        description: formData.description,
        amount: parseFloat(formData.amount),
        paidBy: formData.paidBy,
        splitWith: formData.splitWith,
      });

      if (result.success) {
        setMessage(' ');
        setEditingId(null);
        setFormData({
          description: '',
          amount: '',
          paidBy: currentUsername,
          splitWith: [],
        });
        setShowAddForm(false);
        await loadExpenses();
        if (onTripsUpdated) onTripsUpdated();
      } else {
        setMessage(` ${result.message}`);
      }
    } else {
      const result = await addExpense(tripId, {
        description: formData.description,
        amount: parseFloat(formData.amount),
        paidBy: formData.paidBy,
        splitWith: formData.splitWith,
        createdBy: currentUsername,
      });

      if (result.success) {
        setMessage(' ');
        setFormData({
          description: '',
          amount: '',
          paidBy: currentUsername,
          splitWith: [],
        });
        setShowAddForm(false);
        await loadExpenses();
        if (onTripsUpdated) onTripsUpdated();
      } else {
        setMessage(` ${result.message}`);
      }
    }
  };

  const handleDeleteExpense = async (expenseId) => {
    const expense = expenses[expenseId];
    
    if (expense.createdBy !== currentUsername && !isAdmin) {
      setMessage(' ');
      return;
    }

    if (!window.confirm('')) {
      return;
    }

    const result = await deleteExpense(tripId, expenseId);
    if (result.success) {
      setMessage(' ');
      await loadExpenses();
      if (onTripsUpdated) onTripsUpdated();
    } else {
      setMessage(` ${result.message}`);
    }
  };

  const handleUpdateTripName = async () => {
    if (!newTripName.trim()) {
      setMessage(' ');
      return;
    }

    const result = await updateTripName(tripId, newTripName);
    if (result.success) {
      setMessage(' ');
      setEditingTripName(false);
      setLocalTrip({ ...localTrip, name: newTripName });
      if (onTripsUpdated) {
        onTripsUpdated();
      }
    } else {
      setMessage(` ${result.message}`);
    }
  };

  const handleUpdateMemberName = async (oldName) => {
    if (!isAdmin) {
      setMessage(' ');
      return;
    }

    if (!newMemberNameValue.trim()) {
      setMessage(' ');
      return;
    }

    const result = await updateMemberName(tripId, oldName, newMemberNameValue);
    if (result.success) {
      setMessage(' ');
      setEditingMember(null);
      
      // 
      const updatedMembers = { ...localTrip.members };
      updatedMembers[newMemberNameValue] = updatedMembers[oldName];
      delete updatedMembers[oldName];
      setLocalTrip({ ...localTrip, members: updatedMembers });
      
      if (onTripsUpdated) {
        onTripsUpdated();
      }
      await loadExpenses();
    } else {
      setMessage(` ${result.message}`);
    }
  };

  const handleRemoveMember = async (tripId, memberName) => {
    if (!isAdmin) {
      setMessage(' ');
      return;
    }

    if (!window.confirm(` ${memberName} `)) {
      return;
    }

    const result = await removeMemberFromTrip(tripId, memberName);
    if (result.success) {
      setMessage(' ');
      
      // 
      const updatedMembers = { ...localTrip.members };
      delete updatedMembers[memberName];
      setLocalTrip({ ...localTrip, members: updatedMembers });
      
      if (onTripsUpdated) {
        onTripsUpdated();
      }
      await loadExpenses();
    } else {
      setMessage(` ${result.message}`);
    }
  };

  const handleUpdateTripEmoji = async () => {
    if (!newEmoji.trim()) {
      setMessage(' ');
      return;
    }

    const result = await updateTripEmoji(tripId, newEmoji);
    if (result.success) {
      setMessage(' ');
      setEditingEmoji(false);
      setLocalTrip({ ...localTrip, emoji: newEmoji });
      if (onTripsUpdated) {
        onTripsUpdated();
      }
    } else {
      setMessage(` ${result.message}`);
    }
  };

  const currentTrip = localTrip;

  const members = Object.keys(localTrip.members || {});
  const debts = calculateDebts(expenses, localTrip.members || {});

  return (
    <div className="min-h-screen w-full bg-gray-50">
      {/* Header */}
      <header className="bg-white border-b-2 border-blue-500 shadow-sm sticky top-0 z-40">
        <div className="max-w-6xl mx-auto px-4 py-4 flex justify-between items-center">
          {editingTripName ? (
            <div className="flex gap-2">
              <input
                type="text"
                value={newTripName}
                onChange={(e) => setNewTripName(e.target.value)}
                className="px-3 py-1 border-2 border-blue-500 rounded"
                autoFocus
              />
              <button
                onClick={handleUpdateTripName}
                className="bg-green-500 hover:bg-green-600 text-white px-3 py-1 rounded text-sm"
              >
                
              </button>
              <button
                onClick={() => setEditingTripName(false)}
                className="bg-gray-400 hover:bg-gray-500 text-white px-3 py-1 rounded text-sm"
              >
                
              </button>
            </div>
          ) : (
            <div className="flex items-center gap-3">
              {editingEmoji ? (
                <div className="flex items-center gap-2">
                  <input
                    type="text"
                    value={newEmoji}
                    onChange={(e) => setNewEmoji(e.target.value)}
                    maxLength="2"
                    className="w-12 text-center text-2xl border-2 border-blue-500 rounded"
                    placeholder=""
                    autoFocus
                  />
                  <button
                    onClick={handleUpdateTripEmoji}
                    className="bg-green-500 hover:bg-green-600 text-white px-2 py-1 rounded text-xs"
                  >
                    
                  </button>
                  <button
                    onClick={() => setEditingEmoji(false)}
                    className="bg-gray-400 hover:bg-gray-500 text-white px-2 py-1 rounded text-xs"
                  >
                    
                  </button>
                </div>
              ) : (
                <>
                  <span 
                    className={`text-4xl ${isAdmin ? 'cursor-pointer hover:opacity-75' : ''}`}
                    onClick={() => isAdmin && setEditingEmoji(true)}
                  >
                    {localTrip.emoji || ''}
                  </span>
                  <div>
                    <h1 className="text-2xl font-bold text-blue-600">{currentTrip.name}</h1>
                    {isAdmin && (
                      <button
                        onClick={() => setEditingTripName(true)}
                        className="text-gray-500 hover:text-gray-700 text-sm"
                      >
                        
                      </button>
                    )}
                  </div>
                </>
              )}
            </div>
          )}
          <button
            onClick={() => {
              if (onTripsUpdated) {
                onTripsUpdated();
              }
              onBack();
            }}
            className="bg-gray-500 hover:bg-gray-600 text-white font-bold py-2 px-4 rounded-lg transition"
          >
            
          </button>
        </div>
      </header>

      {/* Main Content */}
      <main className="max-w-6xl mx-auto px-4 py-8">
        {message && (
          <div
            className={`mb-4 sm:mb-6 p-3 sm:p-4 rounded-lg text-sm sm:text-base ${
              message.includes('')
                ? 'bg-green-50 border-l-4 border-green-500 text-green-700'
                : 'bg-red-50 border-l-4 border-red-500 text-red-700'
            }`}
          >
            {message}
          </div>
        )}

        {/*  */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-4 sm:gap-6 mb-6 sm:mb-8">
          <div className="lg:col-span-2 bg-white rounded-lg shadow-sm p-4 sm:p-6">
            <button
              onClick={() => setShowDebts(!showDebts)}
              className="w-full flex justify-between items-center mb-3 sm:mb-4 hover:opacity-80 transition"
            >
              <h2 className="text-lg sm:text-xl font-bold text-gray-800"> </h2>
              <span className="text-xs sm:text-sm font-semibold text-blue-600 bg-blue-50 px-2 sm:px-3 py-1 rounded">
                {showDebts ? '' : ''}
              </span>
            </button>
            
            {showDebts && (
              <div className="grid grid-cols-2 gap-2 sm:gap-3">
                {members.map(member => (
                  <div
                    key={member}
                    className={`p-2 sm:p-3 rounded-lg flex flex-col justify-between text-sm sm:text-base ${
                      debts[member] > 0
                        ? 'bg-green-50 border-l-4 border-green-500'
                        : debts[member] < 0
                        ? 'bg-red-50 border-l-4 border-red-500'
                        : 'bg-gray-50 border-l-4 border-gray-500'
                    }`}
                  >
                    <span className="font-semibold text-gray-800 truncate mb-1">{member}</span>
                    <span
                      className={`font-bold text-xs sm:text-sm ${
                        debts[member] > 0
                          ? 'text-green-600'
                          : debts[member] < 0
                          ? 'text-red-600'
                          : 'text-gray-600'
                      }`}
                    >
                      {debts[member] > 0
                        ? ` $${debts[member].toFixed(0)}`
                        : debts[member] < 0
                        ? ` $${Math.abs(debts[member]).toFixed(0)}`
                        : ''}
                    </span>
                  </div>
                ))}
              </div>
            )}
          </div>

          {/*  */}
          <div className="bg-gradient-to-br from-blue-500 to-blue-600 text-white rounded-lg shadow-sm p-4 sm:p-6 flex flex-col justify-center">
            <h3 className="text-base sm:text-xl font-bold mb-3 sm:mb-4"></h3>
            <button
              onClick={() => setShowAddForm(!showAddForm)}
              className="w-full bg-white text-blue-600 font-bold py-2 sm:py-3 px-3 sm:px-4 rounded-lg hover:bg-gray-100 transition text-sm sm:text-base"
            >
               
            </button>
          </div>
        </div>

        {/*  */}
        {showAddForm && (
          <div className="bg-white rounded-lg shadow-sm p-4 sm:p-6 mb-6 sm:mb-8">
            <h2 className="text-lg sm:text-xl font-bold text-gray-800 mb-4">
              {editingId ? '' : ''}
            </h2>

            <form onSubmit={handleAddExpense} className="space-y-3 sm:space-y-4">
              <div className="grid grid-cols-1 sm:grid-cols-2 gap-3 sm:gap-4">
                <div>
                  <label className="block text-xs sm:text-sm font-semibold text-gray-700 mb-2"></label>
                  <input
                    type="text"
                    value={formData.description}
                    onChange={(e) => setFormData({...formData, description: e.target.value})}
                    className="w-full px-3 sm:px-4 py-2 border-2 border-gray-200 rounded-lg focus:outline-none focus:border-blue-500 text-sm sm:text-base"
                    placeholder=""
                  />
                </div>

                <div>
                  <label className="block text-xs sm:text-sm font-semibold text-gray-700 mb-2"> (TWD)</label>
                  <input
                    type="number"
                    value={formData.amount}
                    onChange={(e) => setFormData({...formData, amount: e.target.value})}
                    className="w-full px-3 sm:px-4 py-2 border-2 border-gray-200 rounded-lg focus:outline-none focus:border-blue-500 text-sm sm:text-base"
                    placeholder="0"
                  />
                </div>
              </div>

              <div>
                <label className="block text-xs sm:text-sm font-semibold text-gray-700 mb-2"></label>
                <select
                  value={formData.paidBy}
                  onChange={(e) => setFormData({...formData, paidBy: e.target.value})}
                  className="w-full px-3 sm:px-4 py-2 border-2 border-gray-200 rounded-lg focus:outline-none focus:border-blue-500 text-sm sm:text-base"
                >
                  {members.map(member => (
                    <option key={member} value={member}>{member}</option>
                  ))}
                </select>
              </div>

              <div>
                <label className="block text-xs sm:text-sm font-semibold text-gray-700 mb-2"></label>
                <div className="space-y-2 bg-gray-50 p-3 rounded-lg max-h-40 overflow-y-auto">
                  {members.map(member => (
                    <label key={member} className="flex items-center text-sm sm:text-base">
                      <input
                        type="checkbox"
                        checked={formData.splitWith.includes(member)}
                        onChange={(e) => {
                          if (e.target.checked) {
                            setFormData({
                              ...formData,
                              splitWith: [...formData.splitWith, member]
                            });
                          } else {
                            setFormData({
                              ...formData,
                              splitWith: formData.splitWith.filter(m => m !== member)
                            });
                          }
                        }}
                        className="mr-2 w-4 h-4"
                      />
                      <span className="text-gray-700">{member}</span>
                    </label>
                  ))}
                </div>
              </div>

              <div className="flex gap-2 sm:gap-3">
                <button
                  type="submit"
                  className="flex-1 bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-3 sm:px-4 rounded-lg transition text-sm sm:text-base"
                >
                  {editingId ? '' : ''}
                </button>
                <button
                  type="button"
                  onClick={() => {
                    setEditingId(null);
                    setFormData({
                      description: '',
                      amount: '',
                      paidBy: currentUsername,
                      splitWith: [],
                    });
                    setShowAddForm(false);
                  }}
                  className="flex-1 bg-gray-400 hover:bg-gray-500 text-white font-bold py-2 px-3 sm:px-4 rounded-lg transition text-sm sm:text-base"
                >
                  
                </button>
              </div>
            </form>
          </div>
        )}

        {/*  */}
        <div className="bg-white rounded-lg shadow-sm p-4 sm:p-6">
          <h2 className="text-lg sm:text-xl font-bold text-gray-800 mb-4">
             ({Object.keys(expenses).length})
          </h2>

          {Object.keys(expenses).length === 0 ? (
            <p className="text-gray-500 text-center py-6 sm:py-8 text-sm sm:text-base"></p>
          ) : (
            <div className="overflow-x-auto -mx-4 sm:mx-0">
              <table className="w-full text-xs sm:text-sm">
                <thead className="bg-gray-50 border-b sticky top-0">
                  <tr>
                    <th className="px-2 sm:px-4 py-2 sm:py-3 text-left font-semibold text-gray-700"></th>
                    <th className="px-2 sm:px-4 py-2 sm:py-3 text-left font-semibold text-gray-700"></th>
                    <th className="px-2 sm:px-4 py-2 sm:py-3 text-left font-semibold text-gray-700 hidden sm:table-cell"></th>
                    <th className="px-2 sm:px-4 py-2 sm:py-3 text-left font-semibold text-gray-700 hidden lg:table-cell"></th>
                    <th className="px-2 sm:px-4 py-2 sm:py-3 text-left font-semibold text-gray-700 hidden md:table-cell"></th>
                    <th className="px-2 sm:px-4 py-2 sm:py-3 text-left font-semibold text-gray-700 hidden md:table-cell"></th>
                    <th className="px-2 sm:px-4 py-2 sm:py-3 text-left font-semibold text-gray-700"></th>
                  </tr>
                </thead>
                <tbody>
                  {Object.entries(expenses).map(([id, expense]) => (
                    <tr key={id} className="border-b hover:bg-gray-50">
                      <td className="px-2 sm:px-4 py-2 sm:py-3 font-semibold text-gray-800 max-w-20 sm:max-w-none truncate">{expense.description}</td>
                      <td className="px-2 sm:px-4 py-2 sm:py-3 font-semibold text-gray-800 whitespace-nowrap">${expense.amount.toFixed(0)}</td>
                      <td className="px-2 sm:px-4 py-2 sm:py-3 text-gray-700 hidden sm:table-cell text-xs sm:text-sm">{expense.paidBy}</td>
                      <td className="px-2 sm:px-4 py-2 sm:py-3 text-gray-700 text-xs hidden lg:table-cell">{expense.splitWith?.join(', ') || '-'}</td>
                      <td className="px-2 sm:px-4 py-2 sm:py-3 text-gray-700 text-xs hidden md:table-cell">{expense.createdBy || '-'}</td>
                      <td className="px-2 sm:px-4 py-2 sm:py-3 text-gray-600 text-xs hidden md:table-cell">{expense.createdAt ? new Date(expense.createdAt).toLocaleDateString('zh-TW') : '-'}</td>
                      <td className="px-2 sm:px-4 py-2 sm:py-3">
                        {(expense.createdBy === currentUsername || isAdmin) && (
                          <div className="flex gap-1 sm:gap-2">
                            <button
                              onClick={() => {
                                setEditingId(id);
                                setFormData({
                                  description: expense.description,
                                  amount: expense.amount.toString(),
                                  paidBy: expense.paidBy,
                                  splitWith: expense.splitWith || [],
                                });
                                setShowAddForm(true);
                              }}
                              className="bg-blue-500 hover:bg-blue-600 text-white px-2 sm:px-3 py-1 rounded text-xs transition"
                            >
                              
                            </button>
                            <button
                              onClick={() => handleDeleteExpense(id)}
                              className="bg-red-500 hover:bg-red-600 text-white px-2 sm:px-3 py-1 rounded text-xs transition"
                            >
                              
                            </button>
                          </div>
                        )}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>

        {/*  */}
        <div>
          <h3 className="font-semibold text-gray-800 mb-3">
             ({Object.keys(currentTrip.members || {}).length})
          </h3>

          {Object.keys(currentTrip.members || {}).length === 0 ? (
            <p className="text-gray-500 text-sm text-center py-4"></p>
          ) : (
            <div className="overflow-x-auto">
              <table className="w-full text-sm">
                <thead className="bg-gray-50 border-b">
                  <tr>
                    <th className="px-4 py-2 text-left font-semibold text-gray-700"></th>
                    {isAdmin && <th className="px-4 py-2 text-left font-semibold text-gray-700"></th>}
                    <th className="px-4 py-2 text-left font-semibold text-gray-700"></th>
                  </tr>
                </thead>
                <tbody>
                  {Object.entries(currentTrip.members || {}).map(([memberName, memberData]) => (
                    <tr key={memberName} className="border-b hover:bg-gray-50">
                      <td className="px-4 py-2 font-semibold text-gray-800">
                        {editingMember === memberName ? (
                          <input
                            type="text"
                            value={newMemberNameValue}
                            onChange={(e) => setNewMemberNameValue(e.target.value)}
                            className="px-2 py-1 border-2 border-blue-500 rounded"
                            autoFocus
                          />
                        ) : (
                          memberName
                        )}
                      </td>
                      {isAdmin && (
                        <td className="px-4 py-2 text-gray-600 font-mono text-xs">{memberData.password}</td>
                      )}
                      <td className="px-4 py-2 space-x-2">
                        {editingMember === memberName ? (
                          <>
                            <button
                              onClick={() => handleUpdateMemberName(memberName)}
                              className="bg-green-500 hover:bg-green-600 text-white px-2 py-1 rounded text-xs"
                            >
                              
                            </button>
                            <button
                              onClick={() => setEditingMember(null)}
                              className="bg-gray-400 hover:bg-gray-500 text-white px-2 py-1 rounded text-xs"
                            >
                              
                            </button>
                          </>
                        ) : (
                          <>
                            {isAdmin && (
                              <>
                                <button
                                  onClick={() => {
                                    setEditingMember(memberName);
                                    setNewMemberNameValue(memberName);
                                  }}
                                  className="bg-blue-500 hover:bg-blue-600 text-white px-2 py-1 rounded text-xs"
                                >
                                  
                                </button>
                                <button
                                  onClick={() => handleRemoveMember(tripId, memberName)}
                                  className="bg-red-500 hover:bg-red-600 text-white px-2 py-1 rounded text-xs"
                                >
                                  
                                </button>
                              </>
                            )}
                          </>
                        )}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          )}
        </div>
      </main>
    </div>
  );
};
